import { watch } from 'vue';
import {  ElMessageBox } from 'element-plus';
import type Node from 'element-plus/es/components/tree/src/model/node';
import { useStore } from 'vuex';

/**
 * //
 * */

export default (
  cusInfo: any,
  data: any,
  inputRef: any,
  cusInputValue: any,
  emit: any,
  inputIsLoading: any,
  status: any
) => {
  const store = useStore();
  /**
   * <>
   */
  watch(
    () => [store.state.dataIndex.dir_add_status, cusInfo, data],
    (val) => {
      if (cusInfo.value.length && store.state.dataIndex.dir_add_status) {
        store.commit('POP_MESSAGE', {type: 'warning', message:  ''})
        return;
      }
      // trueinput
      if (store.state.dataIndex.dir_add_status && !cusInfo.value.length) {
        const newChild: Tree = {
          id: null,
          name: '',
          children: [],
          is_dir: true,
        };
        cusInfo.value = [newChild]; // 
        data.value.unshift(newChild); // data

        // input
        setTimeout(() => {
          if (!inputRef.value) {
            cusInfo.value = [];
            cusInputValue.value = '';
          }
          inputRef.value && inputRef.value.focus();
        }, 300);
      }
      // <>
      store.commit('dataIndex/set_dir_add_status', false);
    }
  );
  /**
   * <>
   * @param node
   * @param _data
   * @param e
   * @param is_dir
   * @returns
   */
  const append = (node: Node, _data: Tree, is_dir: boolean) => {
    // 
    node.expanded = true;
    store.commit('dataIndex/set_expandIDs', {
      isOpen: true,
      id: _data.id,
    });
    if (cusInfo.value.length) {
      // 
      store.commit('POP_MESSAGE', {type: 'warning', message:  ''})
      return;
    }
    cusInfo.value = [node, _data];
    const newChild: Tree = {
      id: null,
      name: '',
      children: [],
      is_dir,
    };
    if (!_data.children) {
      _data.children = [];
    }
    _data.children.unshift(newChild);
    // input
    setTimeout(() => {
      setTimeout(() => {
        inputRef.value.focus();
      }, 0);
    }, 100);
  };

  /**
   * <>
   * @param node
   * @param _data
   */
  const handleEdit = (node: Node, _data: Tree) => {
    status.value = 'edit';
    cusInfo.value = [node, { ..._data }];
    cusInputValue.value = _data.name;
    _data.name = '';// input
    setTimeout(() => {
      inputRef.value && inputRef.value.focus();
    }, 100);
  };

  return {
    append,
    handleEdit,
  };
};
